
/* global self */
import { log } from './log';
import { getRequestType } from './getRequestType';

export function onFetch(e) {

  const url = e.request.url;
  log('Fetching', url);

  const requestType = getRequestType(url);
  const fetchWithSW = ![ 'page', 'api' ].includes(requestType);
  log('RequestType', requestType);

  if (fetchWithSW) {
    e.respondWith(
      self.caches.match(e.request)
      .then(response => {
        if (response) {
          log('Found in cache', url);
          return response;
        }

        const requestClone = e.request.clone();

        self.fetch(requestClone)
        .then(fetchResponse => {
          if (!fetchResponse) {
            log('No response from fetch');
            return fetchResponse;
          }
          const responseClone = fetchResponse.clone();
          self.caches.open(self.cacheName)
          .then(cache => {
            cache.put(e.request, responseClone);
            return response;
          });
        })
        .catch(error => {
          log('Error Fetching && Catching', error);
          return self.fetch(e.request);
        });
      })
      .catch(error => {
        log('Error Finding in cache', error);
        return self.fetch(e.request);
      })
    );
  }

}


