
/* global self */
import { log } from './log';

export function onActivate(e) {

  log('Activated');
  e.waitUntil(
    self.caches.keys()
    .then(cacheNames => {
      return Promise.all(cacheNames.map(itemChacheName => {
        if (itemChacheName !== self.cacheName) {
          log('Removing cached files from ', itemChacheName);
          return self.caches.delete(itemChacheName);
        }
      }));
    })
    .catch(err => {
      log('Activate: Error', err);
    })
  );

}



